import React, { useState } from "react"
import { Outlet } from "react-router-dom"
import ReactHlsPlayer from "react-hls-player"
import axios from "axios"
import Hls from "hls.js"
import Sidebar from "./components/Sidebar"

function DashBoard() {

    const [username, setUsername] = useState('')
    const playerRef: any = React.useRef();

    const url = "http://localhost:8080/hls/test.m3u8"

    async function checkHLSActive() {
        try {
            let res = await axios.head(url);
            return /2\d\d/.test('' + res.status);
        } catch (err) {
            console.log('err', url, err);
            return false;
        }
    }

    setTimeout(checkHLSActive, 50000)

    React.useEffect(() => {
        fetch("http://localhost:4000/success", {
            credentials: 'include',
        })
            .then(res => res.json())
            .then(data => {
                setUsername(data.displayName)
            })
            .catch(err => console.log(err))
    })

    let message: string = (username)
        ? `hi there: ${username}` : 'not logged in!';

    return (
        <div className="App" style={{
        padding: '50px 0px 0px 370px'}}>
            <Sidebar />
            <Outlet />
            <h1>Dashboard</h1>
            <p>{message}</p>
            <ReactHlsPlayer
                playerRef={playerRef}
                src={url}
                autoPlay={true}
                controls={true}
                height="50%"    
            />


        </div>
    );
}

export default DashBoard;
